@use 'sass:map';

$brk_sp: 768px;
/*  */
 $brk_pc2: 960px;
$brk_pc: 1280px;

/* 2023 SP:0599 TAB:6001024 PC:1025px */

/*========================================
        
========================================*/

$bp:(
sm: 640, /*   Small */
md: 768, /*  md  Medium */
lg: 1024,/*  lg  Large */
xl: 1280 /*  xl  X-Large */
);

/*  bootstrap5 */
//  7681024
$bp_bs5:(
xs: 375, /*  X-small : SP(375) */
sm: 576, /*  Small : SP */
md: 768, /*  md  Medium : SPtab-pcmdSPPC */
lg: 992, /*  lg  Large :  tab-pc */
//md:1024, /*  md  Medium : SPtab-pcmdSPPC */
//lg:1124, /*  lg  Large :  tab-pc */
xl: 1280, /*  xl  X-Large : PC(1280) */
xxl: 1440 /*  xxl XX-Large : PC(1440) */
);

$breakpoints: ( /*   :  */
'sp': 'screen and (max-width: #{$brk_sp - 1px})', /*  767px() */
'sp-tab': 'screen and (max-width: #{$brk_pc - 1px})', /*  () */
'sp-tab2': 'screen and (max-width: #{$brk_pc2 - 1px})', /*  () */
'tab': 'screen and (min-width: #{$brk_sp - 1px}) and (max-width: #{$brk_pc - 1px})', /*  1280px() */
'tab2': 'screen and (min-width: #{$brk_sp - 1px}) and (max-width: #{$brk_pc2 - 1px})',
'tab-pc': 'screen and (min-width: #{$brk_sp - 1px})', /*  768px(PC) */
'pc2': 'screen and (min-width: #{$brk_pc2})', /*  960px(PC) */
'pc': 'screen and (min-width: #{$brk_pc})' /*  1280px(PC) */
) !default;

/*
//  BootStrap5
//   : 
//
// -xx : 0
//  xx : 
//  xx-xx : 
// xx- : 
//
//  
//  1.-md : SP(0767)
//  2.md- : (768)
//  
//  md-lg : SP
//  md-xxl : PC
//  xl- : PCPC
//  xxl- : PC
*/

/* px */
$breakpointsbs: (
/*sp */
'-xs': 'screen and (max-width: #{map-get($bp_bs5, 'xs') - 1px})', /*  SP */
'-sm': 'screen and (max-width: #{map-get($bp_bs5, 'sm') - 1px})', /*  SP */
'md' : 'screen and (min-width: #{map-get($bp_bs5, 'sm') - 0px}) and (max-width: #{map-get($bp_bs5, 'md') - 1px})', /*  SP */
'-md': 'screen and (max-width: #{map-get($bp_bs5, 'md') - 1px})', /*  0md() */
'md-': 'screen and (min-width: #{map-get($bp_bs5, 'md') - 0px})', /*  md() */
/*'md-': 'screen and (min-width: 768px)', //  md() */
/*pc*/
'md-lg' : 'screen and (min-width: #{map-get($bp_bs5, 'md') - 1px}) and (max-width: #{map-get($bp_bs5, 'lg') - 1px})', /*  () */
'-lg'   : 'screen and (max-width: #{map-get($bp_bs5, 'lg') - 1px})', /*   */
'lg'    : 'screen and (min-width: #{map-get($bp_bs5, 'md') - 0px}) and (max-width: #{map-get($bp_bs5, 'lg') - 1px})', /*  */
'lg-'   : 'screen and (min-width: #{map-get($bp_bs5, 'lg') - 0px})', /*  992px(PC) */
'lg-xxl': 'screen and (min-width: #{map-get($bp_bs5, 'md') - 0px}) and (max-width: #{map-get($bp_bs5, 'lg') - 1px})', /* mdlgxl768px(PC) */
'-xl'   : 'screen and (max-width: #{map-get($bp_bs5, 'xl') - 1px})', /*  1280px(PC) */
'xl'    : 'screen and (min-width: #{map-get($bp_bs5, 'lg') - 0px}) and (max-width: #{map-get($bp_bs5, 'xl') - 1px})', /* PCPC */
'xl-'   : 'screen and (min-width: #{map-get($bp_bs5, 'xl') - 0px})', /*  1280px(PC) */
'xxl-'  : 'screen and (min-width: #{map-get($bp_bs5, 'xxl')- 0px})' /*  1440px(PC) */
) !default;

/*----------------------------------------
//        
//----------------------------------------*/
@mixin mq($bpname) {
    /*  map-get(,)
    @media #{map-get( $breakpointsbs, $bpname )} {
     */
    @media #{map-get($breakpointsbs, $bpname)} {
        /*   */
        @content;
    }
}
