$size-font-h2-en:36px;
$size-font-h3-en:20px;
$size-font-h4-en:18px;
$size-font-h5-en:14px;

/*==========================================================================
box shadow
box-shadow https://www.bad-company.jp/box-shadow
==========================================================================*/
$shadow_color: #ccc; /*  */
$shadowNormalLv1: 0 5px 20px $shadow_color;
$shadowNormalLv2: 0 10px 25px 0 $shadow_color;
$shadowAquaLv1: 0 5px 20px rgba(94, 135, 178, 0.4);
$shadowAquaLv2: 0 10px 25px 0 rgba(94, 135, 178, 0.4);
$shadowAquaLv3: 0 15px 25px 5px rgba(94, 135, 178, 0.4);
