@use '../global' as g;

/*--------------------------------------------------
    
--------------------------------------------------*/
/* ----------------------------------------
// 
// fixedp-headerbar
// ----------------------------------------*/
.l-header {
    top: 0; /*  0 */
    left: 0;
    right: 0;
    margin: 0;
    width: 100%;
    z-index: 500;
    position: relative;
    height: g.$header-height;
    display: flex;
    flex-direction: row;
    justify-content: center;
    background-color: transparent;
    transition: opacity 0.2s;
    .l-header__buttonswrapper {
        text-shadow: none;
    }

    /*  fixed*/
    position: fixed;

    /*  */
    &:not(&.open) {
        &[data-float="true"] {
            //position: fixed;
            color: black;
            text-shadow: none;
        }
    }
    &.dwis-active {
        position: fixed;
        top: 0;
    }
}

/* --------------------------------------------
// 
// fixed
// 
// --------------------------------------------*/
body {  
    &[data-float="true"]{
/*      TOP*/
        //margin-top: g.$header-height;

/*      TOPY0*/
/*      margin-top: 0;*/
    }
    &.dwis-active {
        position: fixed;
    }
}


/* ============================================
// 
// ============================================*/
@include g.mq('-md') {
/*  */
    .l-header {
        height: g.$header-height-sp;
    }
    body {
/*      */
        &.l-header__float {
            //margin-top: calc( g.$header-height-sp + g.$header-second-height-sp );
        }
    }
}