@use '../../global' as g;
//------------------------------------------------
//    
//------------------------------------------------
/**/
.c-animobj__scrolldown{
    position:absolute;
//    bottom:10px;
//    left:50%;
    //  
    span{
        /**/
        position: absolute;
        left:10px;
        bottom:10px;
        color: g.$color-key;
        @include g.fz(12,12);
        @include g.ls(20);
        /**/
        -ms-writing-mode: tb-rl;
        -webkit-writing-mode: vertical-rl;
        writing-mode: vertical-rl;
    }
    &.--vertical{
        span{
            color : g.$color-white;
            position : relative;
            left:-45%;
            top:-80px;
            bottom :0;
        }
    }


    /*  */
    &:before{
        content: "";
        /**/
        position: absolute;
        bottom:0;
        left:-4px;
        /**/
        width:10px;
        height:10px;
        border-radius: 50%;
        background:g.$color-white;
        /*1.6*/
        animation:
        circlemove 1.6s ease-in-out infinite,
        cirlemovehide 1.6s ease-out infinite;
    }
    /**/
    @keyframes circlemove{
        0%{bottom:75px;}
        100%{bottom:-5px;}
    }
    /**/
    @keyframes cirlemovehide{
        0%{opacity:0}
        50%{opacity:1;}
        80%{opacity:0.9;}
        100%{opacity:0;}
    }
    /*  */
    &:after{
        content:"";
        /**/
        position: absolute;
        bottom:0;
        left:0;
        /**/
        width:2px;
        height: 80px;
        background:g.$color-white;
    }
}



@include g.mq('-md') {
    .c-animobj__scrolldown{
        &.--vertical{
            span{
                top:-50px;
            }
        }
        /**/
        @keyframes circlemove{
            0%{bottom:45px;}
        }
        /*  */
        &:after{
            height: 50px;
        }
    }
    
}

