@use '../global' as g;
//-------------------------------------------
//
//  
//  l- layout
//
//-------------------------------------------
@mixin l-sec--flags() {
    &.--left{  justify-content: flex-start;  }
    &.--right{ justify-content: flex-end; }
    &.--center{justify-content: center;   }
}
@mixin l-sec--pcflags() {
    &.--pc-left{  justify-content: flex-start;  }
    &.--pc-right{ justify-content: flex-end; }
    &.--pc-center{justify-content: center; }
}
@mixin l-sec--spflags() {
    &.--sp-left{  justify-content: flex-start;  }
    &.--sp-right{ justify-content: flex-end; }
    &.--sp-center{justify-content: center;  }
}


.l-section {
    position: relative;
    width: 100%;
    display:flex;
    flex-direction:column;
    align-items : center;
    //  
    &.--ofh{
        overflow: hidden;
    }
}

//  
.l-heading {
    position: relative;
    width: 100%;
    display:flex;
    @include l-sec--flags();
    //  
    &.--ofh{
        overflow: hidden;
    }
}

//  
//  (CV)
//  
.l-body {
    position:relative;
    margin:auto;
    //max-width: g.$layout-width-inner;   /*   */
    max-width: 100%;
    width: 100%;
    /*   */
    &.--ofh{
        overflow: hidden;
    }
}

//  
//  
.l-inner {
    position:relative;
    margin:auto;
    max-width: g.$layout-width-inner;   /*   */
    width: 100%;
    //max-width: 100%;
    /*   */
    &.--ofh{
        overflow: hidden;
    }
}


//  
//  
.l-btn{
    position: relative;
    width: 100%;
    display:flex;
    @include l-sec--flags();
}

//  
.l-wrapper{
    position: relative;
    width: 100%;
    display:flex;
    @include l-sec--flags();
}

//----------------------------------------
//  
//----------------------------------------
//----------------------------------------
//  PCCSS 768px
//----------------------------------------
@include g.mq('md-') {
    .l-heading,
    .l-inner,
    .l-btn,
    .l-wrapper{
        @include l-sec--pcflags();
    }
}

@include g.mq('-xl') {
}

@include g.mq('-lg') {
}

//----------------------------------------
//  SP 767
//----------------------------------------
@include g.mq('-md') {      
    .l-heading,
    .l-inner,
    .l-btn,
    .l-wrapper{
        @include l-sec--spflags();
    }
    
}
